  border-radius: 100px;
  gap: 10px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14.06px;
  transition: background-color .3s, color .3s, transform .3s;
}

.header-btn:hover {
  color: #fff;
  background-color: #000;
  transform: translateY(-3px);
}

.header-btn:active {
  transform: translateY(0);
}

.header-btn.active, .header-btn.active:hover {
  color: #fff;
  background-color: #000;
}

#home-btn {
