.search-form {
  position: relative;
}

.search-form input {
  width: 252px;
  height: 34px;
  color: #000;
  text-align: left;
  background-color: #fff;
  border: none;
  border-radius: 100px;
  outline: none;
  padding-left: 14px;
  padding-right: 137px;
  font-size: 12px;
  font-weight: 400;
  line-height: 114.06px;
  transition: box-shadow .3s, transform .3s;
}

.search-form input:focus {
  transform: scale(1.05);
  box-shadow: 0 0 10px rgba(0, 0, 0, .2);
}

.search-button {
  cursor: pointer;
  fill: #fff;
  background: none;
  border: none;
  position: absolute;
  top: 50%;
  right: 13px;
  transform: translateY(-50%);
}

.search-icon {
  width: 14px;
  height: 14px;
  stroke: #000;
}

