  color: #fff;
  margin-top: 8px;
  margin-left: 14px;
  font-size: 10px;
  font-weight: 400;
  line-height: 11.72px;
}

.logo-container {
  opacity: 0;
  align-items: center;
  gap: 8px;
  margin-top: auto;
  margin-left: auto;
  margin-right: auto;
  animation: fadeIn 1s ease-out forwards;
  display: flex;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.logo-icon {
  width: 32px;
  height: 32px;
  fill: #fff;
  transition: transform .5s;
}

.logo-container:hover .logo-icon {
  transform: rotate(360deg);
  box-shadow: 0 8px 12px rgba(0, 0, 0, .6);
}

.logo-text {
  color: #fff;
  text-align: center;
  opacity: 0;
  font-size: 32px;
  font-weight: 500;
  line-height: 37.5px;
  transition: text-shadow .3s, transform .5s, color .3s;
  animation: fadeIn 1.2s ease-out forwards;
}

.logo-container:hover .logo-text {
  text-shadow: 2px 4px 6px rgba(0, 0, 0, .6);
  color: #f0f0f0;
  transform: scale(1.1);
}

